<template>
  <div class="roadmap">
    <div class="roadmap-text">ROADMAP</div>
    <div class="rm-line"></div>

    <div class="rm">
      <div class="rm-container">
        <div class="arrow-right"></div>

        <div class="rm-box-pl" v-for="item in roadmap" :key="item.title">
          <div class="rm-box-point"></div>
          <div class="rm-box-line"></div>
          <div class="rm-box">
            <img class="rm-img" :src="item.imgUrl" alt="" />
            <div class="rm-text-main">{{ item.title }}</div>
            <div class="rm-text">
              <p>{{ item.text }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      roadmap: [
        {
          title: "launch",
          text: "Membership Pass launch and marketing",
          imgUrl: require("../assets/launch.png"),
        },
        {
          title: "Podcast",
          text: "Podcast officially launching",
          imgUrl: require("../assets/launch.png"),
        },
        {
          title: "WhitePaper",
          text: "Publish Whitepaper and tokenomics",
          imgUrl: require("../assets/launch.png"),
        },
        {
          title: "Airdrop",
          text: "Genesis XIE DAO NFT Drop",
          imgUrl: require("../assets/launch.png"),
        },
        {
          title: "Charity",
          text: "Charity activities and Southeast Asia subdao",
          imgUrl: require("../assets/launch.png"),
        },
      ],
    };
  },
  mounted() {
    this.gsap
      .timeline({
        scrollTrigger: {
          trigger: ".roadmap-text",
          start: "center bottom",
          end: "bottom top",
          scrub: true,
        },
      })
      .to(".roadmap-text", {
        backgroundPosition: "0% 100%",
      });

    this.gsap
      .timeline({
        scrollTrigger: {
          trigger: ".rm-container",
          start: "top center",
        },
      })
      .from([".rm-container"], {
        width: 1,
        duration: 1,
      })
      .from([".rm-box-pl"], {
        autoAlpha: 0,
        stagger: 0.3,
      });
  },
};
</script>

<style>
.roadmap {
  text-align: center;
}
.roadmap-text {
  font-size: 150px;
  font-family: plaak;
  color: #e6e2dd;
  border: 5px solid #282828;
  background: url(../assets/roadmap-background.jpg);
  background-size: 100%;
}
.arrow-right {
  position: absolute;
  right: -20px;
  top: -42px;
  background-image: url("../assets/triangle.svg");
  background-size: 100%;
  width: 70px;
  height: 70px;
}
.rm {
  display: flex;
  justify-content: center;
  margin: 100px 0;
}
.rm-container {
  width: 1400px;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  border-top: 10px solid #303030;
  margin-top: 50px;
  margin-bottom: 118px;
}

.rm-box-pl {
  position: relative;
  width: 200px;
}
.rm-box-point {
  border-radius: 50%;
  width: 26px;
  height: 26px;
  background: #e6e2dd;
